<template>
  <div class="my">
    <div class="myBox" :style="{backgroundImage:'url('+userInfo.userBg+')'}">
      <div class="change-bg clearfix">
        <div class="fr cbg" v-if="isLogin">
          <div class="bg">
            <svg
              t="1594430284967"
              class="icon"
              viewBox="0 0 1193 1024"
              version="1.1"
              xmlns="http://www.w3.org/2000/svg"
              p-id="2209"
              width="16"
              height="16"
            >
              <path
                d="M1169.653917 244.680286L948.398106 23.408614A79.630523 79.630523 0 0 0 885.737242 0.285471C883.532772 0.095157 881.39174 0 879.298287 0H761.414662l-8.262797 4.599253c-43.677049 24.296746-99.391454 37.6663-156.897982 37.666301S483.001231 28.896 439.308323 4.630972l-8.262797-4.599253h-117.867765a80.312482 80.312482 0 0 0-71.050537 42.440008L23.408932 261.158301A79.40849 79.40849 0 0 0 0.000317 317.68154a79.40849 79.40849 0 0 0 23.408615 56.475661l159.800269 159.847848a79.297474 79.297474 0 0 0 49.259591 23.075565v386.210417a80.788266 80.788266 0 0 0 80.693109 80.708969h566.183964a80.788266 80.788266 0 0 0 80.708969-80.708969V540.681896a79.297474 79.297474 0 0 0 49.782954-23.154862l159.816129-159.80027a80.027011 80.027011 0 0 0 0-113.046478z"
                p-id="2210"
                fill="#a52a2a"
              />
            </svg>
          </div>
          <van-uploader class="uploadBox" :max-size="maxSize" :after-read="uploadFile" />
        </div>
      </div>
      <div class="user-info">
        <div v-if="isLogin">
          <div class="clearfix">
            <div class="my-img fl">
              <img class="auto-img" :src="userInfo.userImg" />
            </div>
            <div class="nickName fl">{{userInfo.nickName}}</div>
          </div>
          <div class="text">简介：{{userInfo.desc == ''?'这个人很懒，什么都没有留下':userInfo.desc}}</div>
        </div>
        <div class="login-text" v-else @click="goPage('Login')">登录查看更多信息！</div>
      </div>
    </div>
    <div>
      <van-cell-group>
        <van-cell
          class="cell"
          @click="goPage(item.routeName)"
          :title="item.title"
          is-link
          v-for="(item, index) in listData"
          :key="index"
        />
      </van-cell-group>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      //上传文件限制大小1mb
      maxSize: 1 * 1024 * 1024,
      listData: [
        {
          title: "账户管理",
          routeName: "Account"
        },
        {
          title: "地址管理",
          routeName: "MyAddress"
        },
        {
          title: "我的收藏",
          routeName: "Like"
        },
        {
          title: "安全中心",
          routeName: "Secure"
        }
      ],
      userInfo: {},
      isLogin: false
    };
  },
  created() {
    this.getUserInfo();
    if (localStorage.getItem("__TK")) {
      this.isLogin = true;
    }
  },
  methods: {
    //跳转页面
    goPage(name) {
      if (name === "Login") {
        this.$router.push({ name });
      } else {
        if (!this.isLogin) {
          this.$toast("请先登录");
        } else {
          this.$router.push({ name });
        }
      }
    },
    //获取用户信息
    getUserInfo() {
      //获取token
      let tokenString = localStorage.getItem("__TK");
      //开启加载提示
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        //延迟自动关闭加载提示，如果该值为0，则不会自动关闭
        duration: 0
      });

      this.axios({
        method: "GET",
        url: "/findMy",
        params: {
          appkey: this.appkey,
          tokenString
        }
      })
        .then(result => {
          //关闭加载提示
          this.$toast.clear();

          if (result.data.code == "A001") {
            this.userInfo = result.data.result[0];
          }
        })
        .catch(err => {
          //关闭加载提示
          this.$toast.clear();
        });
    },
    //获取上传文件的base64
    uploadFile(file) {
      //获取图片base64
      let serverBase64Img = file.content.replace(
        /^data:image\/[A-Za-z]+;base64,/,
        ""
      );
      //获取图片类型
      let imgType = file.file.type.split("/")[1];
      //获取token
      let tokenString = localStorage.getItem("__TK");
      //开启加载提示
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        //延迟自动关闭加载提示，如果该值为0，则不会自动关闭
        duration: 0
      });

      this.axios({
        method: "POST",
        url: "/updateUserBg",
        data: {
          appkey: this.appkey,
          tokenString,
          imgType,
          serverBase64Img
        }
      })
        .then(result => {
          //关闭加载提示
          this.$toast.clear();

          if (result.data.code == "I001") {
            this.userInfo.userBg = result.data.userBg;
          } else {
            this.$toast(result.data.msg);
          }
        })
        .catch(err => {
          //关闭加载提示
          this.$toast.clear();
        });
    }
  }
};
</script>

<style lang="less" scoped>
.my {
  position: fixed;
  left: 0;
  top: 0;
  bottom: 0;
  right: 0;
  background-color: #fff;
  .myBox {
    background-color: rgba(215, 180, 148, 0.3);
    padding: 20px;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center center;
    height: 130px;
  }
  .change-bg,
  .text {
    font-size: 14px;
    color: #fff;
    height: 30px;
    line-height: 30px;
  }
  .my-img {
    background-color: #fff;
    width: 60px;
    height: 60px;
    border-radius: 50%;
    box-shadow: 0 0 0 4px rgba(215, 180, 148, 0.4);
    overflow: hidden;
    margin-top: 10px;
    img {
      height: 60px;
    }
  }
  .nickName {
    top: 10px;
    color: #fff;
    font-size: 40px;
    margin-top: 0px;
    margin-left: 10px;
    text-shadow: 2px 2px 2px brown;
  }
  .text {
    font-size: 16px;
    margin-top: -22px;
    margin-left: 70px;
    text-shadow: 2px 2px 2px rgb(215, 180, 148);
  }
  .cbg {
    position: relative;
  }
  .uploadBox {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 30px;
    overflow: hidden;
    opacity: 0;
  }
  .login-text {
    font-size: 20px;
    line-height: 70px;
    text-align: center;
    letter-spacing: 3px;
    color: brown;
  }
  /deep/ .van-cell-group {
    padding: 10px;
  }
  .cell {
    margin-bottom: 10px;
    border: 1px solid rgb(215, 180, 148);
    border-radius: 10px;
  }
}
</style>